export default class SocialShare {
  constructor(el) {
    this.el = el

    this.setVariables()
    this.addListeners()
  }

  setVariables() {
    this.height = 255
    this.width = 630

    this.url = this.el.getAttribute('href')
  }

  addListeners() {
    this.el.addEventListener('click', this.openWindow.bind(this))
  }

  // opens a centered popup window
  openWindow(event) {
    let left = screen.width / 2 - this.width / 2
    let top = screen.height / 2 - this.height / 2

    window.open(
      this.url,
      '',
      'width=' +
        this.width +
        ',height=' +
        this.height +
        ', top=' +
        top +
        ', left=' +
        left
    )

    event.preventDefault()
  }
}



